@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;900&display=swap');

@font-face {
  font-family: mediumFont;
  src: url("./font/Calibre-Medium-568dce56f869a519a015d3b69443b067.woff2") format("woff");
}

@font-face {
  font-family: regularFont;
  src: url("./font/Calibre-Regular-b0d4cbb1201155ddccc21d1b8059a670.woff2") format("woff");
}

@font-face {
  font-family: Mono;
  src: url("./font/SFMono-Regular-8799e6387338d58f2f137df821c86eb4.woff2") format("woff");
}



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
