.loader {
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100vh;
  display: grid;
  place-items: center;
  overflow: hidden;
  transition: all 0.5s ease;
  z-index: 4;
  background: #0a192f;
  ;
}

.loader.fade {
  opacity: 0;
}

.loader.fade.hide {
  display: none;
}

.circle {
  width: 15vw;
  height: 15vw;
  aspect-ratio: 1/1;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  transition: 0.5s cubic-bezier(0.645,0.045,0.355,1);
}

.circle.scale-up {
  transform: scale(8);
}

.box {
  position: absolute;
  top: auto;
  /* left: 0; */
  bottom: -120%;
  --size: 30px;
  --p: 50px;
  --R: 58.3px;
  /* sqrt(var(--p) * var(--p) + var(--size)*var(--size)) */

  height: 140%;
  width: 600vw;
  transform-origin: center center;
  transform: rotate(180deg);
  -webkit-mask:
    radial-gradient(var(--R) at 50% calc(100% - (var(--size) + var(--p))), #00ff 99%, #0000 101%) calc(50% - 2*var(--size)) 0/calc(4*var(--size)) 100%,
    radial-gradient(var(--R) at 50% calc(100% + var(--p)), #0000 99%, #f00f 101%) 50% calc(100% - var(--size))/calc(4*var(--size)) 100% repeat-x;
  mask:
    radial-gradient(var(--R) at 50% calc(100% - (var(--size) + var(--p))), #00ff 99%, #0000 101%) calc(50% - 2*var(--size)) 0/calc(4*var(--size)) 100%,
    radial-gradient(var(--R) at 50% calc(100% + var(--p)), #0000 99%, #f00f 101%) 50% calc(100% - var(--size))/calc(4*var(--size)) 100% repeat-x;
  background: linear-gradient(to top, rgb(4, 133, 149) 30%, rgb(156, 154, 2, 0));
  border: none;
  animation: fill-up 3s 1 normal forwards, flow 28s infinite normal linear;
}

.box:nth-child(2) {
  background: linear-gradient(to top, rgb(0 255 251) 30%, rgb(145 0 255));
  animation: fill-up 3s 1 normal forwards, flow 28s infinite reverse linear;
}

@keyframes fill-up {
  from {
    bottom: -120%;
  }

  to {
    bottom: 0%;
  }
}

@keyframes flow {
  from {
    transform: rotate(180deg) translateX(0%);
  }

  to {
    transform: rotate(180deg) translateX(96%);

  }
}

@media only screen and (max-width: 768px) {

  .box,
  .box:nth-child(2) {
    --size: 15px;
    --p: 30px;
    --R: 33.54px;
    animation-duration: 3s, 18s;
    /* sqrt(var(--p) * var(--p) + var(--size)*var(--size)) */
    /* animation-iteration-count: 1; */
    /* animation-fill-mode: forwards; */
  }
  .circle {
    width: 30vw;
    height: 30vw;
  }
  .circle.scale-up {
    transform: scale(10);
  }
}